import React from 'react';
import './button.css';

interface ButtonProps {
  
  /**
   * What background color to use
   */
  backgroundColor?: string;

  /**
   * 버튼 테두리 라운드 설정
   */  
  borderRadius?: string;  
    
  /**
   * Button contents
   */
  label: string;
  /**
   * Optional click handler
   */
  onClick?: () => void;
}

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  backgroundColor,
    label,
  borderRadius,
  ...props
}: ButtonProps) => {


  return (
    <button
      type="button"
          style={{
              backgroundColor,
              borderRadius
          }}
      {...props}
    >
      {label}
    </button>
  );
};
